@mixin m1024 {
	@media (max-width: 1024px) { @content; }
}





@function vw($px) {
	@return (($px/1920) * 100)+vw;
}


@function vw2($px) {
	@return (($px/400) * 100)+vw;
}

@function vw3($px) {
	@return (($px/1080) * 100)+vw;
}

@function vw4($px) {
	@return (($px/925) * 100)+vw;
}
